import React, { useEffect, useContext, useState } from 'react'
import { NavLink, useHistory } from "react-router-dom";
import ScrollToTop from "react-scroll-to-top";
import { AuthContext } from '../../contexts/Auth/AuthContext';

const Header = () => {
  const auth = useContext(AuthContext);
  const navigate = useHistory();

  const [user, setUser] = useState(null);

  const handleLogout = async () => {
    await auth.signout();
    navigate.push('/');
  }

  useEffect(() => {
    setUser(auth.user);

  }, [auth.user]);

  return (
    <header>
      <div className="header-style-four">
        <ScrollToTop smooth top="100" color="#000" />
        <div className="header-top-area t-header-top-area d-none d-lg-block">
          <div className="container custom-container-two">
            <div className="row align-items-center">
              <div className="col-lg-6 d-none d-lg-block">
              </div>
              <div className="col-lg-6">
                <div className="header-top-login">
                  <ul>
                    {!auth.user && <>
                      <li><NavLink to="/login"><i className="fas fa-sign-in-alt" />Login</NavLink></li>
                    </>}
                    {
                      auth.user && <>
                        <li className="username"><span>Hello, {auth.user.username}!</span></li>
                        <li><a onClick={handleLogout}><i className="fas fa-sign-out-alt" />Exit</a></li>
                      </>
                    }
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>


      </div>

    </header>
  )
}

export default Header