import React, { useState, useEffect } from 'react'

import {useApi} from '../../hooks/useApi';

const MyAccount = () => {

  const api = useApi();

  const [user, setUser] = useState({});

  useEffect(() => {
    getUserData();  
  }, []);

  const getUserData = async () => {

    const token = localStorage.getItem('authToken');
    const user = await api.me(token);
    setUser(user.data.response);
  }

  return (
    <>
      {user && 
        <section className="game-single-area pt-60 pb-60">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <div className="game-single-content game-overview-content">                               
                  <div className="game-single-title mt-50 mb-30">
                    <h4>User info</h4>
                  </div>
                  <div className="game-single-info mb-45">
                    <ul>
                      <li><span>User :</span> { user.userid }</li>
                    </ul>
                  </div>                
                </div>
              </div>
            </div>
          </div>
        </section>
      }
    </>    
  )
}

export default MyAccount