import React, { useState, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import { AuthContext } from '../../contexts/Auth/AuthContext';

const Login = () => {
  const auth = useContext(AuthContext);
  const navigate = useHistory();

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const [error, setError] = useState('');

  const handleLogin = async () => {
    setError('');
    if(username && password) {
      const isLogged = await auth.signin(username, password);

      if(isLogged) {
        navigate.push('/market');
      } else {
        setError('Invalid username or password');
      }
    }
  }

  return (
    <div className='contact-area pt-120 pb-120'>
      <div className='container'>

        <div className="contact-form">
          <div className='row'>
            <div className='col-md-12'>
              <div className='section-title title-style-three mb-40'>
                <h2><span>Login</span> to use system</h2>                
              </div>
              <input type="text" value={ username } onChange={e => setUsername(e.target.value)} placeholder="User" />
              <input type="password" value={ password } onChange={e => setPassword(e.target.value)} placeholder="Password" />
              <button onClick={ handleLogin }>Login</button>
              { error && <div className="alert alert-danger mt-2" role="alert">{error}</div> }
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Login